:root {
  --white: #FFFFFF;
  --white-l: #F6F6F6;
  --white-s: #FAFAFA;
  --pink: #F1CDB3;
  --pink-l: #FDDCC4;
  --dark-s: #CDCDCD;
  --dark-m: #B2B2B2;
  --dark-l: #545454;
  --dark-xs: #4C4C4C;
  --dark-2x1: #444444;
  --dark-3x1: #292929;
}

/*=============== FONTS ===============*/
@font-face {
  font-family: "Arial";
  font-display: swap;
  src: url("../../assets/fonts/ArialMT.woff") format("woff"), url("../../assets/fonts/ArialMT.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Arial";
  font-display: swap;
  src: url("../../assets/fonts/Arial-ItalicMT.woff") format("woff"), url("../../assets/fonts/Arial-ItalicMT.woff2") format("woff2");
  font-weight: 400;
  font-style: italic;
}
@font-face {
  font-family: "Georgia";
  font-display: swap;
  src: url("../../assets/fonts/Georgia-Bold.woff") format("woff"), url("../../assets/fonts/Georgia-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Georgia";
  font-display: swap;
  src: url("../../assets/fonts/Georgia.woff") format("woff"), url("../../assets/fonts/Georgia.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Georgia";
  font-display: swap;
  src: url("../../assets/fonts/Georgia-Italic.woff") format("woff"), url("../../assets/fonts/Georgia-Italic.woff2") format("woff2");
  font-weight: 400;
  font-style: italic;
}
*,
*:before,
*:after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

h2, h3, h4, h5 {
  font-family: "Georgia";
  font-weight: 400;
  color: var(--dark-l);
}

h3, h4, h5 {
  letter-spacing: 0.06em;
}

p {
  color: var(--dark-xs);
}

h2 {
  font-size: 25px;
  line-height: 130%;
}
@media (min-width: 48rem) {
  h2 {
    font-size: 44px;
  }
}

h3 {
  font-size: 25px;
  line-height: 130%;
}
@media (min-width: 48rem) {
  h3 {
    font-size: 35px;
  }
}

h4 {
  font-size: 20px;
  line-height: 115%;
}

h5 {
  font-size: 15px;
  line-height: 110%;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img, video {
  max-width: 100%;
  height: auto;
}

button, input {
  border: none;
  transition: all 0.3s;
}

button {
  cursor: pointer;
}

input {
  outline: none;
}

body {
  overflow-x: hidden;
  font-family: "Arial";
  font-weight: 400;
  color: var(--white);
  font-size: 15px;
}

.body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}

.body--hidden {
  overflow-y: hidden;
}

.section {
  padding-top: 42px;
  padding-bottom: 42px;
}
@media (min-width: 48rem) {
  .section {
    padding-top: 80px;
    padding-bottom: 100px;
  }
}

@media (min-width: 18.75rem) {
  .container {
    width: 100vw;
    max-width: 767px;
    margin-inline: auto;
    padding-right: 10px;
    padding-left: 10px;
  }
}
@media (min-width: 48rem) {
  .container {
    max-width: 1279px;
    padding-right: 30px;
    padding-left: 30px;
  }
}
@media (min-width: 80rem) {
  .container {
    max-width: 1280px;
    padding-right: 40px;
    padding-left: 40px;
  }
}

.title {
  margin-inline: auto;
  text-align: center;
  font-family: "Georgia";
  font-style: normal;
  font-weight: 400;
  letter-spacing: 0.06em;
}
.title span {
  display: block;
}
@media (min-width: 48rem) {
  .title {
    height: 90px;
  }
}

.overlay {
  position: fixed;
  width: 0%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: -100%;
  z-index: 100;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
  background-color: rgba(41, 41, 41, 0.6);
}
.overlay--active {
  opacity: 1;
  visibility: visible;
  right: 0;
  width: 100%;
}
.overlay--active:hover ~ .popup > .popup__inner > .popup__close {
  background-color: var(--pink-l);
}

.btn-fill,
.btn-out {
  display: inline-block;
  color: var(--dark-3x1);
  border-radius: 100px;
  padding: 15px 45px;
  font-family: "Georgia";
  font-weight: 400;
  font-size: 17px;
  line-height: 130%;
  letter-spacing: 0.06em;
  transition: all 0.3s;
}

.btn-fill {
  background-color: var(--pink);
}
.btn-fill:hover {
  background-color: var(--pink-l);
}

.btn-out {
  background-color: transparent;
  border: 2px solid var(--pink);
  width: 187px;
  height: 52px;
  padding: 0;
}
.btn-out:hover {
  border-color: var(--pink-l);
  background-color: var(--pink-l);
}

.slider {
  display: flex;
  align-items: center;
  width: 270px;
  margin-inline: auto;
  overflow: hidden;
}
.slider__item {
  display: flex;
  align-items: center;
  row-gap: 48px;
  margin-inline: auto;
  width: 270px;
}
@media (max-width: 767px) {
  .slider__item {
    max-width: 320px;
    justify-content: center;
  }
}
@media (min-width: 48rem) {
  .slider__item {
    column-gap: 40px;
    width: 580px;
  }
}
@media (min-width: 80rem) {
  .slider__item {
    column-gap: 90px;
    width: 990px;
  }
}
@media (min-width: 48rem) {
  .slider {
    width: 580px;
  }
}
@media (min-width: 80rem) {
  .slider {
    width: 990px;
  }
}
.slider__inner {
  display: flex;
  transition: all 1s;
  position: relative;
  left: -270px;
}
@media (min-width: 48rem) {
  .slider__inner {
    left: -580px;
  }
}
@media (min-width: 80rem) {
  .slider__inner {
    left: -990px;
  }
}
.slider--transition-right {
  animation: move-right 1s;
}
.slider--transition-left {
  animation: move-left 1s;
}

@keyframes move-right {
  from {
    left: -270px;
  }
  to {
    left: -540px;
  }
}
@media (min-width: 48rem) {
  @keyframes move-right {
    from {
      left: -580px;
    }
    to {
      left: -1160px;
    }
  }
}
@media (min-width: 80rem) {
  @keyframes move-right {
    from {
      left: -990px;
    }
    to {
      left: -1980px;
    }
  }
}
@keyframes move-left {
  from {
    left: -270px;
  }
  to {
    left: 0;
  }
}
@media (min-width: 48rem) {
  @keyframes move-left {
    from {
      left: -580px;
    }
    to {
      left: 0;
    }
  }
}
@media (min-width: 80rem) {
  @keyframes move-left {
    from {
      left: -990px;
    }
    to {
      left: 0;
    }
  }
}
.arrow-btns {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  width: 100%;
  top: 52%;
  transform: translateY(-43%);
  z-index: -1;
}
@media (max-width: 767px) {
  .arrow-btns {
    bottom: 95px;
    width: 184px;
    margin-inline: auto;
    text-align: center;
    right: clamp(58px, 100vw - 320px, 68px);
    top: inherit;
    transform: none;
    z-index: 1;
  }
}
.arrow-btns__left, .arrow-btns__right {
  background-color: transparent;
  border: 2px solid var(--pink);
  border-radius: 100px;
  width: 52px;
  height: 52px;
}
.arrow-btns__left--disable {
  pointer-events: none;
}
.arrow-btns__right--disable {
  pointer-events: none;
}
.arrow-btns__left:hover, .arrow-btns__right:hover {
  background-color: var(--pink-l);
  border-color: var(--pink-l);
}

.popup {
  position: fixed;
  z-index: 102;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotateY(-70deg);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
}
.popup__inner {
  position: fixed;
  z-index: 102;
  top: 50%;
  left: 50%;
  width: 240px;
  box-shadow: 0px 2px 35px 14px rgba(13, 13, 13, 0.04);
  transform: translate(-50%, -50%);
  background-color: var(--white-s);
  border-radius: 9px;
}
.popup--active {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, -50%) rotateY(0);
}
@media (min-width: 48rem) {
  .popup__inner {
    width: 630px;
  }
}
.popup__close {
  position: absolute;
  right: -30px;
  top: -52px;
  width: 52px;
  height: 52px;
  border: 2px solid var(--pink);
  box-sizing: border-box;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s;
}
.popup__close:hover {
  background-color: var(--pink-l);
}
@media (min-width: 48rem) {
  .popup__close {
    right: -42px;
    top: -52px;
  }
}
.popup__wrapper {
  display: flex;
  column-gap: 11px;
}
.popup__left-item {
  display: none;
}
@media (min-width: 48rem) {
  .popup__left-item {
    display: block;
    width: 350px;
    height: 350px;
    border-radius: 9px;
  }
  .popup__left-item img {
    width: 100%;
    object-fit: contain;
    border-bottom-left-radius: 9px;
    border-top-left-radius: 9px;
  }
}
.popup__right-item {
  max-width: 240px;
  padding: 10px;
  color: #000;
}
@media (min-width: 48rem) {
  .popup__right-item {
    max-width: 260px;
    padding: 0;
  }
}
.popup__title {
  font-size: 35px;
  line-height: 130%;
  margin-bottom: 10px;
  text-align: center;
}
@media (min-width: 48rem) {
  .popup__title {
    margin-top: 10px;
    text-align: left;
  }
}
.popup__subtitle {
  font-size: 20px;
  line-height: 115%;
  margin-bottom: 20px;
  letter-spacing: 0.06em;
  text-align: center;
}
@media (min-width: 48rem) {
  .popup__subtitle {
    text-align: left;
  }
}
.popup__descr {
  margin-bottom: 24px;
  font-family: "Georgia";
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 110%;
  letter-spacing: 0.06em;
  text-align: justify;
}
@media (min-width: 48rem) {
  .popup__descr {
    margin-bottom: 20px;
    text-align: left;
  }
}
.popup__list {
  list-style-type: disc;
  list-style: inside;
  display: flex;
  flex-direction: column;
  row-gap: 6px;
}
.popup__list-item::marker {
  color: var(--pink);
  background-color: var(--pink);
}
.popup__list-item {
  font-family: "Georgia";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 110%;
  letter-spacing: 0.06em;
}
.popup__list-item span {
  font-family: "Georgia";
  font-style: normal;
  font-weight: 700;
  font-size: 15px;
  line-height: 110%;
  letter-spacing: 0.06em;
  margin-left: -5px;
}
@media (min-width: 48rem) {
  .popup__list-item span {
    margin-left: -7px;
  }
}

.header {
  height: 120px;
  position: absolute;
  display: flex;
  align-items: center;
  top: 0;
  right: 0;
  left: 0;
  background-color: transparent;
  transition: all 0.3s;
}
@media (min-width: 48rem) {
  .header {
    margin-inline: auto;
  }
}
@media (min-width: 80rem) {
  .header {
    margin-inline: 0;
    height: 180px;
  }
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  position: relative;
}
@media (min-width: 48rem) {
  .header__wrapper {
    max-width: 708px;
    margin-inline: auto;
  }
}
@media (min-width: 80rem) {
  .header__wrapper {
    max-width: 100%;
  }
}
.header__logo {
  transition: all 0.3s;
  cursor: pointer;
  text-align: right;
}
.header__logo:hover {
  opacity: 0.6;
  transform: scale(1.1);
}
.header__logo-title {
  color: var(--pink);
  font-family: "Georgia";
  font-weight: 400;
  font-size: 32px;
  line-height: 110%;
  letter-spacing: 0.06em;
  margin: 0 0 8px 0;
}
.header__logo-subtitle {
  font-size: 13px;
  line-height: 15px;
  letter-spacing: 0.1em;
  color: var(--white);
}
.header__nav .header__logo {
  display: none;
}
@media screen and (max-width: 767px) {
  .header__nav {
    position: fixed;
    top: 0;
    width: 0px;
    height: 100vh;
    opacity: 0;
    visibility: hidden;
    overflow: hidden;
    right: 0%;
    transform: translateX(150%);
    animation: slide-right-out 0.3s forwards 0.6s;
    background-color: var(--dark-3x1);
    justify-content: center;
    transition: 0.3s;
    transition-delay: 0.3s;
    z-index: 100;
  }
  .header__nav .header__logo {
    display: block;
    max-width: 195px;
    position: absolute;
    top: 30px;
    left: 10px;
  }
}
.header__nav--open {
  visibility: visible;
  width: 320px;
  opacity: 1;
  transform: translateX(0%);
  animation: slide-right-in 0.3s forwards 0s;
}
.header__nav--open .header__nav-item {
  animation: slide-right-in 0.3s forwards 0s;
}
.header__nav--open .header__nav-item:nth-child(1) {
  animation: slide-right-in 0.3s forwards 0.1s;
  right: -150%;
}
.header__nav--open .header__nav-item:nth-child(2) {
  animation: slide-right-in 0.3s forwards 0.2s;
  right: -150%;
}
.header__nav--open .header__nav-item:nth-child(3) {
  animation: slide-right-in 0.5s forwards 0.3s;
  right: -150%;
}
.header__nav--open .header__nav-item:nth-child(4) {
  animation: slide-right-in 0.5s forwards 0.4s;
  right: -150%;
}
.header__nav--active {
  z-index: 10;
  right: 0;
}
.header__nav-list {
  display: flex;
  gap: 36px;
  margin-top: -4px;
}
@media screen and (max-width: 767px) {
  .header__nav-list {
    flex-direction: column;
    row-gap: 40px;
    align-items: center;
    height: 100vh;
    justify-content: center;
  }
}
@media (max-width: 767px) {
  .header__nav-item {
    position: relative;
    transition: all 0.3s;
    animation: slide-right-out 0.3s forwards 0.5s;
  }
  .header__nav-item:nth-child(1) {
    animation: slide-right-out 0.3s forwards 0.4s;
  }
  .header__nav-item:nth-child(2) {
    animation: slide-right-out 0.3s forwards 0.3s;
  }
  .header__nav-item:nth-child(3) {
    animation: slide-right-out 0.3s forwards 0.2s;
  }
  .header__nav-item:nth-child(4) {
    animation: slide-right-out 0.3s forwards 0.1s;
  }
