    line-height: 160%;
  }
}
.header__nav-link:hover {
  color: var(--white-s);
}
.header__nav-link::after {
  position: absolute;
  content: "";
  bottom: -7px;
  left: 0;
  right: 0;
  width: 0%;
  border-bottom: 3px solid var(--pink);
  transition: all 0.3s;
}
.header__nav-link:hover::after {
  width: 100%;
}
.header__nav-link--active {
  color: var(--white);
}
.header__nav-link--active::after {
  width: 100%;
}
.header__nav-burger {
  z-index: 100;
  position: absolute;
  top: 50%;
  transform: translateY(-50%) rotate(0deg);
  right: 43px;
  display: none;
  width: 30px;
  cursor: pointer;
  transition: all 0.3s;
}
.header__nav-burger span {
  height: 2px;
  background-color: var(--pink);
  width: 100%;
}
@media screen and (max-width: 767px) {
