    display: flex;
    flex-direction: column;
    row-gap: 10px;
  }
}
.header__nav-burger--open {
  transform: translateY(-50%) rotate(-90deg);
}
@keyframes slide-right-in {
  from {
    right: -150%;
  }
  to {
    right: 0;
  }
}
@keyframes slide-right-out {
  from {
    right: 0;
  }
  to {
    right: -150%;
  }
}
@keyframes slide-in {}
@keyframes slide-out {}
@keyframes rotate {}

